// GRID : begin
// main parent container (glass, container)
@mixin glass($max-width)
  width: 100%
  max-width: $max-width
  margin-left: auto
  margin-right: auto
  padding-left: 20px
  padding-right: 20px
  position: relative
  @include breakpoint($desktop-max-1024)
    padding-left: 32px
    padding-right: 32px
  @include breakpoint($tablet-max-768)
    padding-left: 24px
    padding-right: 24px
  //@include breakpoint($mobile-max-375)
// GRID : end
// ICONS
@mixin icon
  display: block
  background-position: center center
  background-repeat: no-repeat
  background-size: contain
// FORM ELEMENTS : begin
// placeholder
@mixin placeholder
  &::-webkit-input-placeholder
    @content
  &::-moz-placeholder
    @content
  &:-moz-placeholder
    @content
  &:-ms-input-placeholder
    @content
  &:focus::-webkit-input-placeholder
    color: transparent
  &:focus::-moz-placeholder
    color: transparent
  &:focus:-moz-placeholder
    color: transparent
  &:focus:-ms-input-placeholder
    color: transparent
// text input default
@mixin default-text-input
  appearance: none
  user-select: none
  background: transparent
  border-radius: 0
  padding: 0 0 3px 0
  font-family: $font-rubik
  font-style: normal
  font-weight: 600
  font-size: 20px
  line-height: 1
  letter-spacing: -0.3px
  color: $color-black-secondary
  border: none
  border-bottom: 1px solid $color-gray-quinary
  text-align: left
  text-transform: none
  width: 100%
  outline: none
  margin: 0
  display: block
  transition: color 0.3s ease, border-color 0.3s ease
  @include breakpoint($tablet-max-768)
    font-size: 18px
  @include placeholder
    color: $color-gray-quinary
    transition: color 0.3s ease, border-color 0.3s ease
  &:hover
    border-color: $color-gray-primary
  &:focus
    border-color: $color-black-secondary
    color: $color-black-secondary
//  &--error
//    border-color: $color-red-100
//    box-shadow: 0 0 0 2px $color-red-100
// textarea default
@mixin default-textarea
  appearance: none
  user-select: none
  background: transparent
  border-radius: 0
  padding: 0 0 3px 0
  font-family: $font-rubik
  font-style: normal
  font-weight: 600
  font-size: 20px
  line-height: 1
  letter-spacing: -0.3px
  color: $color-black-secondary
  border: none
  border-bottom: 1px solid $color-gray-quinary
  text-align: left
  text-transform: none
  width: 100%
  outline: none
  margin: 0
  display: block
  transition: color 0.3s ease, border-color 0.3s ease
  resize: none
  @include breakpoint($tablet-max-768)
    font-size: 18px
  @include placeholder
    color: $color-gray-quinary
    transition: color 0.3s ease, border-color 0.3s ease
  &:hover
    border-color: $color-gray-primary
  &:focus
    border-color: $color-black-secondary
    color: $color-black-secondary
//  //&--error
//  //  @include placeholder
// button default
@mixin btn-default
  position: relative
  user-select: none
  display: inline-block
  text-transform: none
  background: $color-red-primary
  border-radius: 32px
  font-family: $font-rubik
  font-style: normal
  font-weight: 500
  font-size: 22px
  color: $color-white-primary
  letter-spacing: 0
  width: 100%
  height: auto
  border: none
  line-height: 1
  padding: 21px 36px
  cursor: pointer
  outline: none
  text-decoration: none
  text-align: center
  opacity: 1
  will-change: background
  transition: background 0.24s ease
  @include breakpoint($desktop-max-1024)
    font-size: 18px
    padding: 19px 30px
  @include breakpoint($mobile-max-375)
  &:disabled,
  &[disabled]
    opacity: 0.3
    cursor: default
    &:hover
      opacity: 0.3
      background: $color-red-primary !important
  &:hover
    background: $color-red-secondary
// button green
@mixin btn-default-green
  position: relative
  user-select: none
  display: flex
  justify-content: center
  align-items: center
  text-transform: none
  background: $color-green-secondary
  border-radius: 32px
  font-family: $font-rubik
  font-style: normal
  font-weight: 600
  font-size: 16px
  color: $color-white-primary
  letter-spacing: 0
  width: 100%
  height: auto
  border: none
  line-height: 1
  padding: 20px 36px
  cursor: pointer
  outline: none
  text-decoration: none
  text-align: center
  opacity: 1
  will-change: background
  transition: background 0.24s ease
  @include breakpoint($desktop-max-1024)
    font-size: 18px
    padding: 19px 30px
  //@include breakpoint($mobile-max-375)
  &:before
    content: ''
    @include icon-success
    margin: 0 12px 0 0
  &:disabled,
  &[disabled]
    opacity: 0.3
    cursor: default
    &:hover
      opacity: 0.3
      background: $color-green-primary
  &:hover
