// POSITION CHILD AT CENTER : begin
@mixin pos-all-center
  position: absolute
  left: 50%
  top: 50%
  transform: translate3d(-50%, -50%, 0)
@mixin pos-horizontal-center
  position: relative
  left: 50%
  transform: translate3d(-50%, 0, 0)
@mixin pos-vertical-center
  position: relative
  top: 50%
  transform: translate3d(0, -50%, 0)
// POSITION CHILD AT CENTER : end
// OTHER
@mixin selectionText
  ::-moz-selection
    background: rgba($color-black-primary,0.9)
    color: $color-white-primary
    -webkit-text-fill-color: $color-white-primary
    -webkit-opacity: 1
  ::selection
    background: rgba($color-black-primary,0.89)
    color: $color-white-primary
    -webkit-text-fill-color: $color-white-primary
    -webkit-opacity: 1
    -webkit-text-fill-color: $color-white-primary
@mixin hover
  opacity: 1
  transition: opacity 0.2s ease
  will-change: opacity
  &:hover
    opacity: 0.75
.hidden
  display: none
.scroll-disable
  overflow-y: hidden
