@mixin icon-logo
  @include icon
  background-image: url('data:image/png;base64,')
  width: 146px
  min-width: 146px
  height: 70.96px
  min-height: 70.96px
  @include breakpoint($tablet-max-768)
    width: calc(146px / 1.5)
    min-width: calc(146px / 1.5)
    height: calc(70.96px / 1.5)
    min-height: calc(70.96px / 1.5)
  @include breakpoint($mobile-max-375)
    width: calc(146px / 2)
    min-width: calc(146px / 2)
    height: calc(70.96px / 2)
    min-height: calc(70.96px / 2)
