.advantages
  padding: 200px 0 200px 0
  @include breakpoint($desktop-max-1280)
    padding: 134px 0 134px
  @include breakpoint($desktop-max-1024)
    padding: 100px 0 60px
  @include breakpoint($tablet-max-960)
    padding: 74px 0 14px
  @include breakpoint($tablet-max-768)
    padding: 58px 0 0
  @include breakpoint($mobile-max-375)
  &__container
    @include glass(1790px)
  &__row
    display: flex
    justify-content: space-between
    align-items: flex-start
    margin: 0 0 180px
    column-gap: 24px
    @include breakpoint($desktop-max-1280)
      margin: 0 0 120px
      column-gap: 32px
    @include breakpoint($desktop-max-1024)
      margin: 0 0 96px
    @include breakpoint($tablet-max-960)
      margin: 0 0 66px
      column-gap: 24px
    @include breakpoint($tablet-max-768)
      justify-content: flex-start
      flex-direction: column
      column-gap: 0
      row-gap: 32px
      margin: 0 0 32px
    @include breakpoint($mobile-max-375)
      margin: 0 0 24px
      row-gap: 24px
    &--align-center
      justify-content: center
    &:last-child
      margin-bottom: 0
  &__item
    width: 100%
    max-width: 692px
    display: flex
    justify-content: center
    align-items: center
    @include breakpoint($tablet-max-768)
      max-width: 100%
      justify-content: flex-start
      align-content: flex-start
    @include breakpoint($mobile-max-375)
  &__col
    &:first-child
      margin: 0 24px 0 0
      @include breakpoint($desktop-max-1280)
        margin: 0 16px 0 0
      @include breakpoint($tablet-max-960)
        margin: 0 12px 0 0
    &:last-child
  &__icon
    &--rocket
      @include icon-rocket
    &--star
      @include icon-star
    &--heart
      @include icon-heart
    &--cabbage
      @include icon-cabbage
  &__title
    font: 700 20px/1 $font-rubik
    color: $color-black-primary
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.05)
    margin: 0 0 16px
    display: flex
    justify-content: flex-start
    align-items: center
    @include breakpoint($desktop-max-1280)
      font-size: 28px
      text-shadow: 0 3px 3px rgba(0, 0, 0, 0.05)
      min-height: 64px
      margin: 0 0 6px
    @include breakpoint($desktop-max-1024)
      font: 600 24px/1.04 $font-rubik
      text-shadow: 0 2px 2px rgba(0, 0, 0, 0.05)
      min-height: 60px
    @include breakpoint($tablet-max-960)
      font: 600 20px/1.14 $font-rubik
      min-height: 56px
    @include breakpoint($tablet-max-768)
      min-height: auto
      margin: 0 0 10px
    @include breakpoint($mobile-max-375)
      font: 600 18px/1.14 $font-rubik
      margin: 0 0 8px
    &--big
      font-size: 35px
      @include breakpoint($tablet-max-768)
        font: 600 20px/1.14 $font-rubik
        min-height: auto
        margin: 0 0 10px
      @include breakpoint($mobile-max-375)
        font: 600 18px/1.14 $font-rubik
        margin: 0 0 8px
  &__text
    font: 400 20px/1.16 $font-rubik
    color: $color-gray-quaternary
    margin: 0 0 36px
    @include breakpoint($desktop-max-1280)
      font: 400 22px/1.28 $font-rubik
      margin: 0 0 16px
    @include breakpoint($desktop-max-1024)
      font: 400 20px/1.29 $font-rubik
    @include breakpoint($tablet-max-960)
      font: 400 17px/1.3 $font-rubik
      margin: 0
    @include breakpoint($tablet-max-768)
      font: 400 15px/1.25 $font-rubik
    @include breakpoint($mobile-max-375)
      font: 400 14px/1.5 $font-rubik
    &--selected
      font: 700 30px/1.166 $font-rubik
      color: $color-black-primary
      display: flex
      @include breakpoint($desktop-max-1280)
        font: 600 22px/1.28 $font-rubik
      @include breakpoint($desktop-max-1024)
        font: 500 20px/1.29 $font-rubik
      @include breakpoint($tablet-max-960)
        font: 500 18px/1.29 $font-rubik
        margin: 8px 0 0 0
      @include breakpoint($tablet-max-768)
        font: 500 15px/1.25 $font-rubik
      @include breakpoint($mobile-max-375)
        font: 400 14px/1.5 $font-rubik
    &--small
      font: 400 15px/1.16 $font-rubik
      color: rgba($color-black-primary,0.5)
      width: 100%
      max-width: 300px
      @include breakpoint($desktop-max-1280)
        font: 400 16px/1.24 $font-rubik
        margin: 12px 0 0 0
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-960)
        font: 400 15px/1.25 $font-rubik
        margin: 10px 0 0 0
      @include breakpoint($tablet-max-768)
        max-width: 100%
        margin: 0 0 0 0
      @include breakpoint($mobile-max-375)
        font: 400 14px/1.5 $font-rubik
    &:last-child
      margin-bottom: 0
