.logo
  @include icon-logo
  opacity: 0
  visibility: hidden
  transform: translate3d(-10px, 0, 0)
  will-change: opacity
  transition: visibility 0s ease 0s, opacity 0.5s ease, transform 0.5s ease
  @include breakpoint($desktop-max-1024)
  @include breakpoint($tablet-max-768)
  @include breakpoint($mobile-max-375)
  &--active
    visibility: visible
    opacity: 1
    transform: translate3d(0, 0, 0)
    transition: visibility 0s ease 0s, opacity 0.5s ease, transform 0.5s ease
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-768)
    @include breakpoint($mobile-max-375)
  &:hover
    opacity: 0.8
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-768)
    @include breakpoint($mobile-max-375)
