.careers
  padding: 144px 0
  @include breakpoint($desktop-max-1280)
  @include breakpoint($desktop-max-1024)
    padding: 76px 0
  @include breakpoint($tablet-max-960)
  @include breakpoint($tablet-max-768)
    padding: 58px 0 0
  @include breakpoint($mobile-max-640)
  @include breakpoint($mobile-max-480)
  @include breakpoint($mobile-max-375)
    padding: 28px 0 0
  &__container
    @include glass(1158px)
  &__row
    margin: 0 0 46px
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
      margin: 0 0 32px
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
    @include breakpoint($mobile-max-375)
    &:last-child
      margin-bottom: 0
  h1
    font: 500 42px/1.23 $font-rubik
    color: $color-black-secondary
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
      font: 500 32px/1.25 $font-rubik
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
    @include breakpoint($mobile-max-375)
  &__tabs-controls
    display: flex
    justify-content: flex-start
    align-items: center
    column-gap: 64px
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
      column-gap: 36px
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
      column-gap: 24px
    @include breakpoint($mobile-max-375)
  &__tabs-controls-item
    font: 600 35px/1 $font-rubik
    color: $color-gray-secondary
    will-change: color
    transition: color 0.24s ease
    cursor: pointer
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
      font: 600 20px/1 $font-rubik
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
      font: 600 15px/1 $font-rubik
    @include breakpoint($mobile-max-375)
    &--active
      color: $color-red-primary
      cursor: default
    &:hover
      color: $color-red-primary
  &__text
    font: 500 16px/1.12 $font-rubik
    letter-spacing: -0.38px
    color: $color-gray-quaternary
    width: 100%
    max-width: 556px
    margin: 0 0 46px
    @include breakpoint($mobile-max-640)
      font-weight: 400
    @include breakpoint($mobile-max-480)
      font: 400 15px/1.5 $font-rubik
      color: $color-black-primary
      margin: 0 0 32px
    @include breakpoint($mobile-max-375)
      font: 400 14px/1.25 $font-rubik
      letter-spacing: -0.2px
      margin: 0 0 16px
    &:last-child
      margin-bottom: 0
  //&__tabs-content
  &__tabs-content-item
    display: none
    &--active
      display: block
  &__form
    width: 100%
    max-width: 626px
  &__form-row
    margin: 0 0 20px
    @include breakpoint($tablet-max-768)
      margin: 0 0 16px
    &--center
      @include breakpoint($tablet-max-768)
        display: flex
    &:last-child
      margin-bottom: 0
  &__input-text
    @include default-text-input
  &__form-label
    font: 500 14px/1.42 $font-rubik
    color: $color-black-primary
    letter-spacing: -0.1px
    margin: 0 0 4px
    &--file
      display: flex
      justify-content: flex-start
      align-items: flex-start
      flex-direction: column
      margin: 0 0 0 0
  &__form-label-wrapper
    display: flex
    justify-content: flex-start
    align-items: center
    margin: 4px 0 0
    position: relative
    width: 100%
    overflow: hidden
    &:after
      content: ''
      display: block
      width: 5%
      height: 100%
      background: linear-gradient(90deg, rgba(255,255,255,0.577468487394958) 0%, rgba(255,255,255,1) 50%, rgba(255,255,255,1) 100%)
      position: absolute
      right: 0
      top: 0
      bottom: 0
      @include breakpoint($mobile-max-640)
        width: 6%
  &__form-label-btn
    background: $color-gray-tertiary
    border-radius: 18px
    font: 500 14px/1 $font-rubik
    letter-spacing: -0.1px
    color: $color-gray-quaternary
    display: inline-flex
    justify-content: center
    align-items: center
    padding: 11px 34px
    width: auto
    margin: 0 12px 0 0
    cursor: pointer
    @include breakpoint($mobile-max-480)
      font-size: 13px
    @include breakpoint($mobile-max-375)
  &__form-label-text
    font: 500 14px/18px $font-rubik
    letter-spacing: -0.38px
    color: $color-gray-secondary
    @include breakpoint($tablet-max-768)
      line-height: 16px
    @include breakpoint($mobile-max-480)
      font: 400 12px/13px $font-rubik
      margin: 0 20px 0 0
  &__input-text
    @include default-text-input
  &__input-file
    display: block
    opacity: 0
    visibility: hidden
    position: absolute
    left: -9999px
    top: -9999px
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
    @include breakpoint($mobile-max-375)
  &__textarea
    @include default-textarea
    scrollbar-width: none
    &::-webkit-scrollbar
      display: none
  &__warning
    display: none
    font: 200 13px/1.2 $font-rubik
    color: $color-red-secondary
    margin: 2px 0 0 0
    &--active
      display: block
  &__btn
    @include btn-default-small
    width: 304px
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
      margin: 0 auto
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
      width: 100%
    @include breakpoint($mobile-max-375)
    &--hidden
      display: none !important
    //&--send
    &--success
      @include btn-default-green
      width: 304px
      cursor: default
      @include breakpoint($tablet-max-768)
        margin: 0 auto
      @include breakpoint($mobile-max-480)
        width: 100%
    &--in-progress
      text-indent: -9999px
      font-size: 0
      display: flex
      justify-content: center
      align-items: center
    .preloader
      display: none
      .careers__btn--in-progress &
        display: block
  //&__vacancies-wrapper
  &__vacancies-item
    display: none
    &--active
      display: block
  h2
    font: 600 18px/1.88 $font-rubik
    color: $color-gray-primary
    letter-spacing: -0.38px
    margin: 0 0 24px
    width: 100%
    max-width: 960px
    @include breakpoint($mobile-max-480)
      font: 600 17px/1.88 $font-rubik
      margin: 0 0 20px
    @include breakpoint($mobile-max-375)
      margin: 0 0 16px
    &:last-child
      margin-bottom: 0
  p
    font: 400 16px/1.12 $font-rubik
    color: $color-black-primary
    letter-spacing: -0.38px
    margin: 0 0 24px
    width: 100%
    max-width: 960px
    @include breakpoint($mobile-max-480)
      font: 400 15px/1.5 $font-rubik
      letter-spacing: -0.15px
      margin: 0 0 16px
    @include breakpoint($mobile-max-375)
      margin: 0 0 16px
    &:last-child
      margin-bottom: 0
  b
    font-weight: 500
  ul
    margin: 0 0 24px 10px
    width: 100%
    max-width: 960px
    @include breakpoint($mobile-max-480)
      margin: 0 0 16px
    @include breakpoint($mobile-max-375)
      margin: 0 0 16px
    &:last-child
      margin-bottom: 0
  li
    font: 400 16px/1.12 $font-rubik
    color: $color-black-primary
    letter-spacing: -0.38px
    @include breakpoint($mobile-max-480)
      font: 400 15px/1.5 $font-rubik
      letter-spacing: -0.15px
    @include breakpoint($mobile-max-375)
      margin: 0 0 16px
  &__select-wrapper
    margin: 0 0 46px
    @include breakpoint($mobile-max-480)
      margin: 0 0 32px
    @include breakpoint($mobile-max-375)
      margin: 0 0 16px
    &:last-child
      margin-bottom: 0
  &__response
    font: 500 14px/1.3 $font-rubik
    color: $color-red-primary
    margin: 20px 0
  //&__select
  .select2
  // container
  .select2-container
    min-width: 626px
    @include breakpoint($desktop-max-1024)
      min-width: 100%
  // title placeholder
  .select2-selection__placeholder,
  .select2-container--default .select2-selection--single .select2-selection__placeholder
    color: $color-gray-primary
  // title close icon
  .select2-selection__clear
    display: none
  // title container
  .select2-container--default .select2-selection--single
    background-color: $color-white-secondary
    border: 1px solid $color-gray-primary
    border-radius: 12px
    @include breakpoint($mobile-max-640)
      border-radius: 8px
  .select2-container--default.select2-container--open.select2-container--below .select2-selection--single,
  .select2-container--default.select2-container--open.select2-container--below .select2-selection--multiple
    border-radius: 12px !important
  .select2-container .select2-selection--single
    height: 48px
    @include breakpoint($tablet-max-768)
      height: 42px
  // title font style
  .select2-container--default .select2-selection--single .select2-selection__rendered
    line-height: 46px
    font-size: 18px
    font-weight: 500
    letter-spacing: -0.3px
    color: $color-gray-primary
    @include breakpoint($tablet-max-768)
      line-height: 40px
    @include breakpoint($mobile-max-640)
      font-size: 14px
      letter-spacing: -0.05px
    @include breakpoint($mobile-max-480)
      font-weight: 400
    @include breakpoint($mobile-max-375)
  // title sizes style
  .select2-container .select2-selection--single .select2-selection__rendered
    padding-left: 16.5px
    padding-right: 20px
    @include breakpoint($mobile-max-480)
      padding-left: 14px
      padding-right: 18px
    @include breakpoint($mobile-max-375)
  // arrow
  .select2-container--default .select2-selection--single .select2-selection__arrow
    height: 100%
    width: 40px
    b
      @include icon-arrow-bottom
      background-color: transparent
      border-color: transparent
      border-style: solid
      border-width: 0
      left: 50%
      margin-left: 0
      margin-top: 0
      position: absolute
      top: 50%
      width: 0
      transform: translate3d(-50%, -50%, 0)
      transition: transform 0.2s ease-in-out
      @include breakpoint($mobile-max-480)
        width: calc(14px / 1.15)
        min-width: calc(14px / 1.15)
        height: calc(8px / 1.15)
        min-height: calc(8px / 1.15)
      @include breakpoint($mobile-max-375)
  .select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow
    b
      background-color: transparent
      border-color: transparent
      border-style: solid
      border-width: 0
      transform-origin: center center
      transform: translate3d(-50%, -50%, 0) rotate(-180deg)
// dropdown
.select2-dropdown
  background-color: $color-white-primary !important
  border: 1px solid $color-gray-primary !important
  border-radius: 12px !important
  overflow: hidden !important
  transform: translate3d(0,12px,0) !important
// dropdown options
.select2-results__options
  &::-webkit-scrollbar
    width: 5px
    border-radius: 1px
  &::-webkit-scrollbar-track
    background-color: transparent
  &::-webkit-scrollbar-thumb
    background: $color-black-primary
// option
.select2-results__option
  font-family: $font-rubik !important
  font-size: 18px !important
  font-style: normal !important
  font-weight: 400 !important
  line-height: 24px !important
  letter-spacing: -0.3px !important
  color: $color-gray-primary !important
  padding: 12px 16px !important
  border-bottom: 1px solid $color-gray-primary !important
  will-change: background, font-weight
  transition: background 0.24s ease, font-weight 0.05s ease
  @include breakpoint($mobile-max-480)
    font-size: 14px !important
    padding: 8px 14px !important
    letter-spacing: -0.15px !important
  @include breakpoint($mobile-max-375)
  &:last-child
    border-bottom: none !important
.select2-container--default .select2-results__option--highlighted.select2-results__option--selectable
  background: $color-gray-tertiary !important
  font-weight: 500 !important
  @include breakpoint($mobile-max-480)
    font-weight: 400 !important
