.header
  padding: 6px 0
  @include breakpoint($desktop-max-1024)
  @include breakpoint($tablet-max-768)
    padding: 10px 0
  @include breakpoint($mobile-max-375)
  &__container
    @include glass(1790px)
    display: flex
    justify-content: flex-start
    align-items: center
    column-gap: 36px
    @include breakpoint($desktop-max-1024)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($mobile-max-375)
  &__col
    position: relative
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-768)
      width: 50%
    @include breakpoint($mobile-max-375)
  .header__btn
    display: none
    position: absolute
    transform: translate3d(0,-50%,0)
    top: 50%
    right: 0
    width: 20px
    height: 14px
    cursor: pointer
    z-index: 9999
    @include breakpoint($tablet-max-768)
      display: block
  &__btn-item
    background-color: black
    height: 2px
    position: absolute
    width: 100%
    left: 0
    transition: all 0.3s ease
    border-radius: 10px
    &:first-child
      top: 0
      .header__btn--active &
        transform: rotate(45deg)
    &:nth-child(2)
      top: 6px
      .header__btn--active &
        opacity: 0
    &:last-child
      top: 12px
      .header__btn--active &
        transform: rotate(-45deg)
    .header__btn--active &:first-child,
    .header__btn--active &:last-child
      top: 6px
      background-color: $color-black-primary
