.nav
  display: flex
  justify-content: flex-start
  align-items: center
  column-gap: 16px
  @include breakpoint($desktop-max-1024)
  @include breakpoint($tablet-max-768)
    flex-direction: column
    justify-content: center
    column-gap: 0
    position: fixed
    left: 0
    right: 0
    top: 0
    bottom: 0
    width: 100vw
    height: 100vh
    z-index: 9998
    visibility: hidden
    opacity: 0
    transform: translate3d(0,-100%,0)
    background: $color-white-primary
    will-change: transform, opacity, visibility
    transition: transform 0.5s ease
  @include breakpoint($mobile-max-375)
  &--active
    @include breakpoint($tablet-max-768)
      visibility: visible
      opacity: 1
      transform: translate3d(0,0,0)
  &__item
    font: 600 16px/2.18 $font-rubik
    color: $color-black-secondary
    text-decoration: none
    padding: 16px 0
    cursor: pointer
    opacity: 0
    visibility: hidden
    will-change: transform, opacity, visibility, color
    transform: translate3d(-12px, 0, 0)
    @include breakpoint($desktop-max-1024)
      font-size: 14px
    @include breakpoint($tablet-max-768)
      text-transform: uppercase
      letter-spacing: 0.1px
      transition: transform 5s ease
      visibility: hidden
      opacity: 0
      transform: translate3d(0, -20px, 0) !important
    @include breakpoint($mobile-max-375)
    &--active,
    &:hover
      color: $color-red-primary
      @include breakpoint($desktop-max-1024)
      @include breakpoint($mobile-max-375)
    .nav--animate &
      visibility: visible
      opacity: 1
      transform: translate3d(0,0,0)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-768)
      @include breakpoint($mobile-max-375)
    &--animate
      @include breakpoint($tablet-max-768)
        transform: translate3d(0, 0, 0) !important
        opacity: 1
    &--disable
      color: $color-gray-secondary
      cursor: default
      &:hover
        color: $color-gray-secondary
    @for $i from 1 to 10
      $delayDesktop: $i * 0.03
      $delayMobile: $i * 0.1
      &:nth-child(#{$i})
        transition: visibility 0s ease 0s, opacity 0.5s ease, transform 0.33s ease-in-out #{$delayDesktop}s, color 0.24s ease
        @include breakpoint($tablet-max-768)
          transition: visibility 0s ease 0s, opacity 1s ease #{$delayMobile}s, transform 0.33s ease-in-out #{$delayMobile}s, color 0.24s ease
  //&[href]
