.cover
  height: 100vh
  position: relative
  padding: 15vh 0
  @include breakpoint($desktop-max-1280)
    padding: 10vh 0
  @include breakpoint($desktop-max-1024)
    padding: 60px 0
    height: auto
    min-height: 455px
    display: flex
    justify-content: center
    align-items: center
  @include breakpoint($tablet-max-768)
    min-height: auto
    padding: 24px 0
  @include breakpoint($mobile-max-375)
  &__container
    @include glass(1700px)
    display: flex
    flex-direction: row
    justify-content: center
    align-items: center
    height: 100%
    @include breakpoint($desktop-max-1024)
      @include glass(1724px)
      column-gap: 40px
      height: auto
      align-items: stretch
    @include breakpoint($tablet-max-768)
      flex-direction: column
      justify-content: flex-start
      align-items: flex-start
    @include breakpoint($mobile-max-375)
  &__col
    &--left
      display: flex
      justify-content: flex-start
      align-items: flex-start
      flex-direction: column
      width: 100%
      max-width: 758px
      margin: 0 4px 0 0
      @include breakpoint($desktop-max-1280)
        min-width: 560px
      @include breakpoint($desktop-max-1024)
        min-width: 547px
        margin: 0 0 0 0
      @include breakpoint($tablet-max-768)
        min-width: 100%
        max-width: 100%
      @include breakpoint($mobile-max-375)
    &--right
      @include breakpoint($desktop-max-1024)
        position: relative
        width: 100%
        max-width: 374px
      @include breakpoint($tablet-max-768)
        max-width: 100%
        display: none
    //@include breakpoint($mobile-max-375)
  h1
    font: 600 42px/1.25 $font-rubik
    color: $color-black-primary
    margin: 0 0 30px
    @include breakpoint($desktop-max-1280)
      font: 600 37px/1.25 $font-rubik
    @include breakpoint($desktop-max-1024)
      font: 500 22px/1.27 $font-rubik
    @include breakpoint($tablet-max-768)
      font: 500 27px/1.18 $font-rubik
    @include breakpoint($mobile-max-375)
      font: 500 27px/1.18 $font-rubik
      margin: 0 0 24px
  img
    display: block
    width: 100%
    height: auto
    @include breakpoint($desktop-max-1024)
      position: absolute
      left: 0
      top: 50%
      transform: translate3d(0,-50%,0)
      width: 568px
    @include breakpoint($tablet-max-768)
      position: relative
      left: 0
      top: 0
      transform: translate3d(0,0,0)
      width: 100%
    @include breakpoint($mobile-max-375)
  &__btn
    @include btn-default
    display: inline-block
    width: auto
    opacity: 0
    visibility: hidden
    transform: translate3d(0, 16px, 0)
    will-change: opacity
    cursor: default
    transition: visibility 0s ease 0s, opacity 0.5s ease 0.7s, transform 0.5s ease 0.7s, background 0.24s ease
    @include breakpoint($mobile-max-480)
      width: 100%
    &:hover
      opacity: 0.3
      background: $color-red-primary
    &--active
      visibility: visible
      opacity: 0.3
      transform: translate3d(0, 0, 0)
      transition: visibility 0s ease 0s, opacity 0.5s ease 0.7s, transform 0.5s ease 0.7s, background 0.24s ease
