.text
  padding: 100px 0
  @include breakpoint($desktop-max-1280)
    padding: 66px 0
  @include breakpoint($desktop-max-1024)
    padding: 54px 0
  @include breakpoint($tablet-max-960)
    padding: 50px 0
  @include breakpoint($tablet-max-768)
    padding: 40px 0 0
  @include breakpoint($mobile-max-375)
    padding: 32px 0 0
  &__container
    @include glass(1000px)
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
    @include breakpoint($mobile-max-375)
  h1
    font: 500 42px/1.23 $font-rubik
    color: $color-black-secondary
    margin: 0 0 46px
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
      font: 500 32px/1.25 $font-rubik
      margin: 0 0 32px
    @include breakpoint($mobile-max-375)
    &:last-child
      margin-bottom: 0
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
      @include breakpoint($mobile-max-375)
  h2
    font: 700 20px/1.6 $font-rubik
    color: $color-black-secondary
    margin: 0 0 8px
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
    @include breakpoint($mobile-max-375)
    &:last-child
      margin-bottom: 0
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
        font: 600 18px/1.33 $font-rubik
        margin: 0 0 16px
      @include breakpoint($mobile-max-375)
  p
    font: 400 20px/1.6 $font-source-serif-pro
    color: $color-black-secondary
    margin: 0 0 24px
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
      font: 400 16px/1.5 $font-source-serif-pro
      margin: 0 0 16px
    @include breakpoint($mobile-max-375)
    &:first-child
      margin-top: 0
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
      @include breakpoint($mobile-max-375)
    &:last-child
      margin-bottom: 0
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
      @include breakpoint($mobile-max-375)
    b
      font-weight: 600
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
      @include breakpoint($mobile-max-375)
    a
      font: 400 20px/1.6 $font-source-serif-pro
      color: $color-black-secondary
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
        font: 400 16px/1.5 $font-source-serif-pro
      @include breakpoint($mobile-max-375)
  ul
    margin: 0 0 24px 18px
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
      margin: 0 18px 16px
    @include breakpoint($mobile-max-375)
    &:first-child
      margin-top: 0
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
      @include breakpoint($mobile-max-375)
    &:last-child
      margin-bottom: 0
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
      @include breakpoint($mobile-max-375)
    li
      font: 400 20px/1.6 $font-source-serif-pro
      color: $color-black-secondary
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
        font: 400 16px/1.5 $font-source-serif-pro
      @include breakpoint($mobile-max-375)
  ol
    margin: 0 0 24px 18px
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
      margin: 0 18px 16px
    @include breakpoint($mobile-max-375)
    &:first-child
      margin-top: 0
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
      @include breakpoint($mobile-max-375)
    &:last-child
      margin-bottom: 0
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
      @include breakpoint($mobile-max-375)
    li
      font: 400 20px/1.6 $font-source-serif-pro
      color: $color-black-secondary
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
        font: 400 16px/1.5 $font-source-serif-pro
      @include breakpoint($mobile-max-375)
