.general-info
  padding: 144px 0
  @include breakpoint($desktop-max-1280)
  @include breakpoint($desktop-max-1024)
    padding: 76px 0
  @include breakpoint($tablet-max-960)
  @include breakpoint($tablet-max-768)
    padding: 58px 0 0
  @include breakpoint($mobile-max-640)
  @include breakpoint($mobile-max-480)
  @include breakpoint($mobile-max-375)
    padding: 28px 0 0
  &__container
    @include glass(1158px)
  h1
    font: 500 42xp/1.23 $font-rubik
    color: $color-black-secondary
    margin: 0 0 46px
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
      font: 500 32px/1.25 $font-rubik
      margin: 0 0 32px
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
    @include breakpoint($mobile-max-375)
    &:last-child
      margin-bottom: 0
  p
    font: 500 16px/1.12 $font-rubik
    color: $color-black-primary
    letter-spacing: -0.38px
    margin: 0 0 24px
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
    @include breakpoint($mobile-max-640)
      font-weight: 400
    @include breakpoint($mobile-max-480)
    @include breakpoint($mobile-max-375)
      font: 400 14px/1.25 $font-rubik
      color: $color-black-primary
      letter-spacing: -0.2px
      margin: 0 0 16px
    &:last-child
      margin-bottom: 0
  &__form
    margin: 46px 0 0 0
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
      margin: 24px 0 0 0
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
      margin: 18px 0 0 0
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
    @include breakpoint($mobile-max-375)
      margin: 16px 0 0 0
  &__form-row
    width: 100%
    display: flex
    justify-content: flex-start
    align-items: flex-start
    column-gap: 24px
    margin: 0 0 20px
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
      flex-direction: column
      column-gap: 0
      margin: 0 0 16px
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
    @include breakpoint($mobile-max-375)
    &--full-width
      display: block
      column-gap: 0
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
      @include breakpoint($mobile-max-640)
      @include breakpoint($mobile-max-480)
      @include breakpoint($mobile-max-375)
    &--center
      justify-content: center
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
      @include breakpoint($mobile-max-640)
      @include breakpoint($mobile-max-480)
      @include breakpoint($mobile-max-375)
    &:last-child
      margin-bottom: 0
  &__form-col
    width: 50%
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
      width: 100%
      margin: 0 0 16px
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
    @include breakpoint($mobile-max-375)
    &:last-child
      @include breakpoint($tablet-max-768)
        margin-bottom: 0
  &__form-label
    font: 500 14px/1.42 $font-rubik
    color: $color-black-primary
    letter-spacing: -0.1px
    margin: 0 0 4px
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
    @include breakpoint($mobile-max-375)
    &--file
      display: flex
      justify-content: flex-start
      align-items: flex-start
      flex-direction: column
      margin: 0 0 0 0
      width: 100%
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
      @include breakpoint($mobile-max-640)
      @include breakpoint($mobile-max-480)
      @include breakpoint($mobile-max-375)
  &__form-label-wrapper
    display: flex
    justify-content: flex-start
    align-items: center
    margin: 4px 0 0
    position: relative
    width: 100%
    overflow: hidden
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
    @include breakpoint($mobile-max-375)
    &:after
      content: ''
      display: block
      width: 5%
      height: 100%
      background: linear-gradient(90deg, rgba(255,255,255,0.577468487394958) 0%, rgba(255,255,255,1) 50%, rgba(255,255,255,1) 100%)
      position: absolute
      right: 0
      top: 0
      bottom: 0
      @include breakpoint($mobile-max-640)
        width: 6%
  &__form-label-btn
    background: $color-gray-tertiary
    border-radius: 18px
    font: 500 14px/1 $font-rubik
    letter-spacing: -0.1px
    color: $color-gray-quaternary
    display: inline-flex
    justify-content: center
    align-items: center
    padding: 11px 34px
    width: auto
    margin: 0 12px 0 0
    cursor: pointer
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
      font-size: 13px
    @include breakpoint($mobile-max-375)
  &__form-label-text
    font: 500 14px/18px $font-rubik
    letter-spacing: -0.38px
    color: $color-gray-secondary
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
      line-height: 16px
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
      font: 400 12px/13px $font-rubik
      margin: 0 20px 0 0
    @include breakpoint($mobile-max-375)
  &__input-text
    @include default-text-input
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
    @include breakpoint($mobile-max-375)
  &__input-file
    display: block
    opacity: 0
    visibility: hidden
    position: absolute
    left: -9999px
    top: -9999px
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
    @include breakpoint($mobile-max-375)
  &__textarea
    @include default-textarea
    scrollbar-width: none
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
    @include breakpoint($mobile-max-375)
    &::-webkit-scrollbar
      display: none
  &__warning
    display: none
    font: 200 13px/1.2 $font-rubik
    color: $color-red-secondary
    margin: 2px 0 0 0
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
    @include breakpoint($mobile-max-375)
    &--active
      display: block
  &__btn
    @include btn-default-small
    width: 304px
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
      margin: 0 auto
    @include breakpoint($mobile-max-640)
    @include breakpoint($mobile-max-480)
      width: 100%
    @include breakpoint($mobile-max-375)
    &--hidden
      display: none !important
    //&--send
    &--success
      @include btn-default-green
      width: 304px
      cursor: default
      @include breakpoint($tablet-max-768)
        margin: 0 auto
      @include breakpoint($mobile-max-480)
        width: 100%
    &--in-progress
      text-indent: -9999px
      font-size: 0
      display: flex
      justify-content: center
      align-items: center
    .preloader
      display: none
      .general-info__btn--in-progress &
        display: block
  &__response
    font: 500 14px/1.3 $font-rubik
    color: $color-red-primary
    margin: 20px 0
