.footer
  padding: 58px 0 20px
  &__container
    @include breakpoint($desktop-max-1024)
      display: flex
      flex-direction: column-reverse
      align-items: flex-start
      justify-content: center
  &__row
    @include glass(1790px)
    display: flex
    justify-content: space-between
    align-items: flex-start
    @include breakpoint($desktop-max-1280)
      column-gap: 40px
    @include breakpoint($desktop-max-1024)
      display: flex
      justify-content: flex-start
      align-items: center
      flex-direction: column
      column-gap: 0
    @include breakpoint($mobile-max-375)
    &--align-right
      justify-content: flex-end
      column-gap: 20px
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
        justify-content: center
        column-gap: 24px
        flex-direction: row
      @include breakpoint($mobile-max-375)
    &:first-child
      padding-bottom: 28px
      border-bottom: 1px solid $color-black-primary
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
        padding-bottom: 0
        border-bottom: none
      @include breakpoint($mobile-max-375)
    &:last-child
      padding-top: 20px
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
        border-top: 1px solid $color-gray-secondary
      @include breakpoint($mobile-max-375)
  &__col
    &--left
      display: flex
      flex-direction: column
      justify-content: flex-start
      align-items: flex-start
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
        align-items: center
        margin: 0 0 20px
      @include breakpoint($mobile-max-375)
    &--center
      display: flex
      flex-direction: column
      justify-content: flex-start
      align-items: flex-start
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
        align-items: center
      @include breakpoint($mobile-max-375)
    &--right
      @include breakpoint($desktop-max-1024)
        margin: 20px 0 56px
  &__social-item
    will-change: opacity
    transition: opacity 0.24s ease
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
    @include breakpoint($mobile-max-375)
    &--youtube
      @include icon-youtube
    &--tiktok
      @include icon-tiktok
    &--vk
      @include icon-vk
    &--ok
      @include icon-ok
    &:hover
      opacity: 0.84
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
      @include breakpoint($mobile-max-375)
  &__nav-item
    font: 400 14px/1.3 $font-rubik
    color: $color-black-primary
    margin: 0 0 2px
    text-decoration-line: underline
    will-change: opacity
    transition: opacity 0.24s ease
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
      margin: 0 0 20px
    @include breakpoint($mobile-max-375)
    &:hover
      opacity: 0.84
    &:last-child
      margin-bottom: 0
  p
    font: 400 14px/1.3 $font-rubik
    color: $color-gray-primary
    text-align: right
    @include breakpoint($desktop-max-1280)
    @include breakpoint($desktop-max-1024)
      text-align: center
      font: 300 14px/1.3 $font-rubik
      color: $color-gray-secondary
    @include breakpoint($mobile-max-375)
