.download-app
  background: $color-red-primary
  position: relative
  @include breakpoint($desktop-max-1280)
  @include breakpoint($desktop-max-1024)
  @include breakpoint($tablet-max-960)
  @include breakpoint($tablet-max-768)
  @include breakpoint($mobile-max-480)
  @include breakpoint($mobile-max-375)
  @include breakpoint($mobile-max-360)
  &__container
    @include glass(1565px)
    display: flex
    justify-content: flex-start
    align-items: flex-start
    position: relative
    z-index: 2
    padding-top: 176px
    padding-bottom: 176px
    @include breakpoint($desktop-max-1280)
      padding-top: 116px
      padding-bottom: 116px
    @include breakpoint($desktop-max-1024)
      padding-top: 56px
      padding-bottom: 56px
      align-items: stretch
    @include breakpoint($tablet-max-960)
    @include breakpoint($tablet-max-768)
    @include breakpoint($mobile-max-480)
      flex-direction: column
      align-items: center
    @include breakpoint($mobile-max-375)
    @include breakpoint($mobile-max-360)
  &__col
    &--left
      width: 100%
      max-width: 865px
      @include breakpoint($desktop-max-1280)
      @include breakpoint($desktop-max-1024)
        display: flex
        justify-content: center
        align-items: center
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
        max-width: 385px
      @include breakpoint($mobile-max-640)
        display: none
      @include breakpoint($mobile-max-480)
        display: flex
      @include breakpoint($mobile-max-375)
      @include breakpoint($mobile-max-360)
    &--right
      width: 100%
      max-width: 700px
      padding: 115px 0 0 0
      @include breakpoint($desktop-max-1280)
        padding: 0 0 0 0
      @include breakpoint($desktop-max-1024)
        max-width: 460px
        display: flex
        flex-direction: column
        justify-content: center
        align-items: flex-start
      @include breakpoint($tablet-max-960)
      @include breakpoint($tablet-max-768)
        max-width: 340px
      @include breakpoint($mobile-max-640)
        max-width: 100%
      @include breakpoint($mobile-max-480)
      @include breakpoint($mobile-max-375)
      @include breakpoint($mobile-max-360)
  &__img
    display: block
    width: 100%
    height: auto
    @include breakpoint($mobile-max-640)
      transform: scale(1.1)
      transform-origin: center center
  h2
    font: 500 50px/1.3 $font-rubik
    color: $color-white-primary
    margin: 0 0 20px
    @include breakpoint($desktop-max-1024)
      font: 500 28px/1.3 $font-rubik
  p
    font: 600 35px/1.14 $font-rubik
    color: rgba($color-white-primary, 0.7)
    margin: 0 0 62px
    @include breakpoint($desktop-max-1024)
      font: 500 18px/1.2 $font-rubik
      margin: 0 0 40px
    @include breakpoint($tablet-max-768)
      margin: 0 0 32px
  &__btns
    display: flex
    justify-content: flex-start
    align-items: flex-start
    column-gap: 16px
    @include breakpoint($desktop-max-1024)
      column-gap: 12px
    @include breakpoint($mobile-max-375)
      column-gap: 8px
  &__btn
    opacity: 0.6
    cursor: default
    filter: grayscale(0.8)
    &--google-play
      @include icon-google-play
    &--app-store
      @include icon-app-store
  &__bg
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 1
    width: 100%
    height: 100%
    overflow: hidden
  &__bg-item
    position: absolute
    &--first
      width: calc(355.2px / 2)
      max-width: calc(355.2px / 2)
      height: calc(389.04px / 2)
      max-height: calc(389.04px / 2)
      background-image: url('../img/download-app/leaves/1.webp')
      background-size: cover
      left: 9.2vw
      top: -110px
      @include breakpoint($desktop-max-1024)
        width: calc(355.2px / 3.75)
        max-width: calc(355.2px / 3.75)
        height: calc(389.04px / 3.75)
        max-height: calc(389.04px / 3.75)
