@mixin icon-heart
  @include icon
  background-image: url('data:image/png;base64,')
  width: 100px
  min-width: 100px
  height: 100px
  min-height: 100px
  @include breakpoint($desktop-max-1280)
    width: 80px
    min-width: 80px
    height: 80px
    min-height: 80px
  @include breakpoint($desktop-max-1024)
    width: 66px
    min-width: 66px
    height: 66px
    min-height: 66px
  @include breakpoint($tablet-max-960)
    width: 56px
    min-width: 56px
    height: 56px
    min-height: 56px
  @include breakpoint($tablet-max-768)
    width: 50px
    min-width: 50px
    height: 50px
    min-height: 50px
  @include breakpoint($mobile-max-375)
    width: 40px
    min-width: 40px
    height: 40px
    min-height: 40px
