@mixin icon-app-store
  @include icon
  background-image: url('data:image/png;base64,')
  width: 283px
  min-width: 283px
  height: 100px
  min-height: 100px
  @include breakpoint($desktop-max-1280)
    width: calc(283px / 1.5)
    min-width: calc(283px / 1.5)
    height: calc(100px / 1.5)
    min-height: calc(100px / 1.5)
  @include breakpoint($desktop-max-1024)
    width: 202px
    min-width: 202px
    height: 74px
    min-height: 74px
  @include breakpoint($tablet-max-960)
    width: calc(283px / 2)
    min-width: calc(283px / 2)
    height: calc(100px / 2)
    min-height: calc(100px / 2)
  @include breakpoint($tablet-max-768)
    width: calc(283px / 2.5)
    min-width: calc(283px / 2.5)
    height: calc(100px / 2.5)
    min-height: calc(100px / 2.5)
  @include breakpoint($mobile-max-375)
    width: 106px
    min-width: 106px
    height: 39px
    min-height: 39px
